		 */
		viewContract() {
			uni.previewImage({
				urls: [this.orderData.contract_url]
			});
		},
		
		/**
		 * Áî≥ËØ∑ÂîÆÂêé
		 */
		applyAfterSale() {
			// ‰∏¥Êó∂Á¶ÅÁî®ÂîÆÂêéÂäüËÉΩÔºåÁ≠âÂæÖÈ°µÈù¢ÂàõÂª∫
			uni.showToast({
				title: 'ÂîÆÂêéÂäüËÉΩÂºÄÂèë‰∏≠',
				icon: 'none'
			});
			// uni.navigateTo({
			// 	url: `/pages/user/after-sale/create?order_id=${this.orderId}`
			// });
		},
		
		/**
		 * Êü•ÁúãË¥®‰øù
		 */
		viewWarranty() {
			uni.showModal({
				title: 'Ë¥®‰øù‰ø°ÊÅØ',
				content: `Ë¥®‰øùÊúüÔºö${this.orderData.warranty_months}‰∏™Êúà\nË¥®‰øùÂºÄÂßãÊó∂Èó¥Ôºö${this.formatTime(this.orderData.completed_at)}`,
				showCancel: false
			});
		},
		
		/**
		 * Ê†ºÂºèÂåñÊó∂Èó¥
		 */
		formatTime(timestamp) {
			if (!timestamp) return '';
			const date = new Date(timestamp);
			const year = date.getFullYear();
			const month = String(date.getMonth() + 1).padStart(2, '0');
			const day = String(date.getDate()).padStart(2, '0');
			return `${year}-${month}-${day}`;
		}
	}
};
